@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;transition:background-color 500ms, color 200ms}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.container{margin:0 auto}@media (min-width: 780px){.container{width:750px}}@media (min-width: 992px){.container{width:970px}}@media (min-width: 1200px){.container{width:1170px}}ul{list-style:none;cursor:pointer}body.night{background-color:#031f30;color:#FFFFFF}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.toggle{display:none;align-items:center;justify-content:space-evenly;width:100px;transition:all 500ms}@media (min-width: 768px){.toggle{display:flex}}.toggle-slider{background-color:#c5e1f7;width:3rem;border-radius:10px}.toggle-slider:hover{cursor:pointer}.toggle-circle{background-color:#2697ED;width:0.9rem;height:0.9rem;border-radius:50%;margin:3px;transition:all 500ms}.toggle-slider.night .toggle-circle{transform:translateX(28px)}

nav .list{color:#FFFFFF;position:absolute;background-color:#2697ED;top:60px;width:100%;left:0;height:0;overflow:hidden;transition:all 500ms}nav .list li{padding:0.625rem}nav .list li:hover{color:#d3d4d4}nav .list.expand{height:250px;z-index:1}nav .list.expand.night{background-color:#0f1c24;border-top:solid 1px #383838}@media (min-width: 768px){nav .list.expand{height:0}}nav .toggle.expand{position:absolute;bottom:1rem;left:0.2rem;display:flex}nav .night{background-color:#031f30}

.hamburger-menu{width:5.44rem;height:19px;padding:1.72rem;background-color:inherit;border:none;cursor:pointer;transition:all 2s}.hamburger-menu:focus{outline:none}.hamburger-menu .bar{width:100%;border:solid 1px white}.hamburger-menu .top{transform:translateY(-5px)}.hamburger-menu .bottom{transform:translateY(5px)}@media (min-width: 768px){.hamburger-menu{display:none}}

*{box-sizing:border-box;margin:0;padding:0}body{font-family:'Open Sans', sans-serif}nav{transition:all 500ms;background-color:#2697ED;width:100%}nav img{padding:1rem;top:0}nav img:hover,nav li:hover{-webkit-filter:invert(10%);filter:invert(10%);cursor:pointer}nav .container{display:flex;justify-content:space-between;align-items:center}.nav-items-container{display:none;height:55px;align-items:center}.nav-items-container li{color:#FFFFFF;font-size:14px;list-style:none;padding:1rem;font-weight:600;white-space:nowrap}.nav-items-container .underline{border-bottom:solid 4px rgba(255,255,255,0.5)}@media (min-width: 768px){.nav-items-container{display:flex}}nav.night{background-color:#0f1c24}nav.night .toggle-slider{background-color:#344658}

.card-link{width:100%;margin:0.4rem;padding:1.5rem;box-shadow:0 3px 6px rgba(11,33,74,0.09),0 -2px 2px rgba(11,33,74,0.03);cursor:pointer;transition:all 500ms}.card-link h3{font-size:1.3rem;font-weight:400;font-family:'Geomanist Regular', sans-serif;margin-top:1rem}.card-link p{font-size:0.875rem;padding:0.5rem 0}.card-link:hover{transition:transform 200ms;transform:scale(1.01)}@media (min-width: 768px){.card-link{width:46.9%}}@media (min-width: 1200px){.card-link{width:33.33%;max-width:377px}}.card-link.night{background-color:#0e161b;box-shadow:none}

.search{text-align:center}.search h2{font-size:2rem;font-weight:400;margin-top:3.5rem;padding:0.8rem}.search input{width:90%;padding:0.8rem;font-size:1.1rem;transition:all 150ms;border:solid 1px #B0B0B0;border-radius:4px}.search input:focus{outline:none;border-color:#2697ED}.search input::-webkit-input-placeholder{color:#B0B0B0}.search input:-ms-input-placeholder{color:#B0B0B0}.search input::-ms-input-placeholder{color:#B0B0B0}.search input::placeholder{color:#B0B0B0}@media (min-width: 778px){.search input{width:525px}}.links-1{display:flex;flex-wrap:wrap;justify-content:space-between;margin:2.5rem 1rem}@media (min-width: 750px){.links-1{margin:2.5rem 0}}.links-2{margin-top:2.5rem}.links-2 .how-do-i,.links-2 .popular-videos{width:100%;background-color:#F2F3F5;transition:background-color 500ms, color 200ms}.links-2 .how-do-i.night,.links-2 .popular-videos.night{background-color:#0e161b}.links-2 .how-do-i{padding:1.9rem}.links-2 .how-do-i .header h3{margin-bottom:0.7rem;font-size:1.25rem;font-weight:600}.links-2 .how-do-i .header p{font-size:0.875rem}.links-2 .how-do-i .list{display:flex;margin-top:0.5rem}.links-2 .how-do-i .list ul{width:50%}.links-2 .how-do-i .list p:hover{color:#23527c;opacity:80%}.links-2 .popular-videos{margin-top:1rem;padding:2rem 2.4rem}.links-2 .video-link{display:flex;align-items:center;margin-top:1rem;cursor:pointer}.links-2 .video-link p{margin-left:0.75rem}.links-2 .video-link:hover{color:#23527c;opacity:80%}@media (min-width: 1200px){.links-2{display:flex;justify-content:space-between}.links-2 .how-do-i{width:66%}.links-2 .how-do-i .header{width:66.66%}.links-2 .popular-videos{margin-top:0;width:33.3%}}.list{color:#2697ED;font-size:0.875rem}

